import { Component, OnInit, Input } from '@angular/core';
import { Transacciones } from '../Transacciones.Service';


@Component({
  selector: 'app-producto-simple',
  templateUrl: './producto-simple.component.html',
  styleUrls: ['./producto-simple.component.css']
})

export class ProductoSimpleComponent implements OnInit {
  @Input() ImagenU: string;
  @Input() Image_Title: string;
  @Input() Pesos: number;
  @Input() ID: string;

  constructor(private Tra:Transacciones) {
  }
  public my_Class = 'productClassSmall';
  ngOnInit() {}
    public CBCx = 0;

    private changeButtonColor(){
      var Button= document.getElementById(this.ID);
      if(this.CBCx==0){
      Button.classList.remove("buttonGris");
      Button.classList.add("buttonColor");
      this.Tra.TotalAPagar=this.Tra.TotalAPagar+this.Pesos;
      this.CBCx=1;
    }
    else{
      Button.classList.remove("buttonColor");
      Button.classList.add("buttonGris");
      this.Tra.TotalAPagar=this.Tra.TotalAPagar-this.Pesos;
      this.CBCx=0;
    }
  }
  

  
}



